.project-detail-page {
    background-color: #ffffff;
    font-family: 'Work Sans', Arial, sans-serif;
    font-size: 16px;
    line-height: 20.8px;
    color: rgb(26, 26, 26);
    font-style: normal;
    font-weight: 500;
    letter-spacing: 1px;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    height: 100vh; /* Set a fixed height */
    overflow: hidden; /* Hide overflow on the main container */
}

.project-display-container {
    margin-top: 5vh;
    flex: 1;
    padding: 5px; /* Further reduced from 10px */
    overflow-y: auto; /* Add scrolling to this container */
    margin-bottom: 5vh;
}

.project-display {
    width: 100%;
    max-width: 1200px; /* Or your preferred maximum width */
    margin: 0 auto;
}

.project-image {
    width: 50%;
    float: left;
    padding-right: 20px;
}

.content-row {
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin-top: 10px;
}

container {
    margin-bottom: 2px; /* Minimal spacing between content elements */
    padding: 0 !important;
    
}

.text-left, .image-left {
    width: 48%; /* Slightly less than half to account for spacing */
    float: left;
}

.text-right, .image-right {
    width: 48%; /* Slightly less than half to account for spacing */
    float: right;
}

.text-full {
    width: 100%;
    max-width: 50% !important;
    clear: both;
    margin-left: auto;
    margin-right: auto;
    float: none;
    margin-bottom: 20px;
}
.container h3 {
    font-family: 'Epilogue', Arial, sans-serif;
    font-weight: 700;
    font-size: 24px;
    color: rgb(43, 44, 50);
    text-align: justify;
    margin-top: 30px;
}


.image-full {
    width: 100%;
    clear: both;
    margin-left: auto;
    margin-right: auto;
    float: none;
    margin-bottom: 20px;
}

.image-left img, .image-right img, .image-full img {
    width: 100%;
    height: auto;
    display: block;
}

.project-header {
    margin-top: 1vh; /* Drastically reduced from 2vh */
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: rgb(255, 255, 255); /* Light gray background */
    padding: 5px; /* Further reduced from 10px */
    border-radius: 8px;
    box-shadow: none; /* Explicitly remove any box shadow */
    margin-bottom: 20px;
}

.project-title-summary {
    
    width: 45%;
    order: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: #000; /* Black text */
    font-size: 1.2em; /* Increase font size for title and summary */
}


.project-title-summary h1 {
    margin: 0;
    padding: 0;
    width: 100%;
    text-align: left; /* Keep the title left-aligned */
    font-family: 'Epilogue', Arial, sans-serif;
    font-weight: 800;
    font-size: 32px;
    line-height: 41.6px;
    color: rgb(43, 44, 50);
    font-style: normal;
    letter-spacing: 0.7px; /* Add slightly more letter spacing for titles */
    margin-bottom: 0.1em; /* Minimal space below title */
}

.project-image {
    width: 50%;
    order: 2;
    padding-left: 20px;
    margin-bottom: 0;
}

.github-button {
    margin-top: 15px; /* Reduced from 15px */
    padding: 10px 20px;
    background-color: #24292e; /* GitHub's color */
    color: white;
    border: none;
    border-radius: 15px;
    font-family: 'Work Sans', Arial, sans-serif;
    font-size: 16px;
    font-weight: 500;
    letter-spacing: 1px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    text-decoration: none; /* Remove underline */
    display: inline-block; /* Ensure proper sizing */
}

.github-button:hover {
    background-color: #464646; /* Slightly lighter on hover */
    transform: translateY(-3px); /* Move the button slightly upwards */
    text-decoration: none; /* Ensure no underline on hover */
    transition: all 0.3s ease; /* Smooth transition for all changes */
}

/* Add more specific styles as needed */

/* Responsive adjustments */
@media (max-width: 768px) {
    .project-header {
        flex-direction: column;
        padding: 2px; /* Minimal padding for mobile */
    }

    .project-image, .project-title-summary {
        width: 100%;
        padding: 0;
    }
    
    .project-image {
        order: 1;
    }

    .project-title-summary {
        order: 2;
    }

    .text-left, .text-right, .image-left, .image-right {
        float: none;
        width: 100%;
        padding: 0;
    }

    /* Ensure text remains justified on smaller screens */
    .project-display, p {
        text-align: justify;
    }

    .github-button {
        margin-top: 10px;
        padding: 8px 16px;
        font-size: 14px;
    }
}

/* Add this new rule to reduce space between paragraphs */
p {
    text-align: justify;
    font-family: 'Work Sans', Arial, sans-serif;
    font-size: 16px;
    line-height: 20.8px;
    color: rgb(26, 26, 26);
    font-style: normal;
    font-weight: 500;
    letter-spacing: 1px; /* Increased from 0.5px to 1px */
    margin-bottom: 15px;
    margin-top: 15px;
}

/* Customize scrollbar for webkit browsers */
::-webkit-scrollbar {
    width: 10px;
}

::-webkit-scrollbar-track {
    background: #f1f1f1;
}

::-webkit-scrollbar-thumb {
    background: #888;
    border-radius: 5px;
}

::-webkit-scrollbar-thumb:hover {
    background: #555;
}

/* For Firefox */
* {
    scrollbar-width: thin;
    scrollbar-color: #888 #f1f1f1;
}

